import React from 'react';
import ReactDOM from 'react-dom';
import styles from './HomeSearchBar.module.scss';

export class HomeSearchBar extends React.Component {
    render() {
        return (
            <div className={styles.searchContainer}>
                <form>
                    <div className={styles.searchBar}>
                        <div className={`${styles.fieldContainer} ${styles.first}`}>
                            <label for="zoneQuery">
                                <div className={styles.fieldTitle} >Zone Gographique</div>
                                <input className={styles.fieldInput} type="text" id='zoneQuery' placeholder='O veux-tu plonger?' >
                                </input>
                            </label>
                        </div>
                        <div className={`${styles.fieldContainer} ${styles.second}`}>
                            <label for="keyWordQuery">
                                <div className={styles.fieldTitle}>Catgorie / Mot-Cl</div>
                                <input className={styles.fieldInput} type='text' id='keyWordQuery' placeholder="Rcif, Grotte, pave..." ></input>
                            </label>
                        </div>
                        <div className={styles.submitContainer}><button className={styles.submitBtn} type='submit' ><img className={styles.searchIcon} src={require('../../assets/search-icon-white.png') } />Rechercher</button>
                        </div>
                    </div>
                </form>
                <div className={styles.quickLinks}>
                    <div className={styles.option}><img className={styles.quickLinksLogo} src={require('../../assets/foot-icon.png')} /><button className={styles.optionBtn}>Accs  pied</button></div>
                    <div className={styles.option}><img className={styles.quickLinksLogo} src={require('../../assets/boat-icon.png')} /><button className={styles.optionBtn} >Accs en bateau</button></div>

                </div>
            </div>
        )
    };
}